
const FoodItemList = (props) => {

    let food = [];
    let expiry = [];
    props.list.forEach(eachItem => {
    food.push(eachItem.foodItem);
    food.push(<br/>);
    expiry.push(eachItem.expiry);
    });

    
      
    return(
        <>
            <div>{food}</div>
        </>
    );
};

export default FoodItemList;