import {Route, Redirect, Switch} from 'react-router-dom';
import { useEffect } from "react";
import './App.css';
import Header from './components/Header.js';
import DashHeader from './components/DashHeader.js';
import Home from './pages/Home.js';
import Dashboard from './pages/Dashboard.js';
import Auth from './pages/Auth.js';
// import Expiry from './pages/Expiry';
// import Recipe from './pages/Recipe';
// import Recommendations from './pages/Recommendations';
import Footer from './components/Footer.js';
import RecipeInfo from './pages/RecipeInfo.js';
import useHttp from './hooks/use-http.js';
import {getAllItems} from "./lib/api.js";
import LoadingSpinner from './UI/LoadingSpinner.js';
import AllRecipes from './pages/AllRecipes.js';

function App() {
  const { sendRequest, status, data: loadedItems, error } = useHttp(
    getAllItems,
    true 
  );  

  useEffect(() => {
    sendRequest();
  }, [sendRequest]);

  if (status === 'pending') {
    return (
      <div className='centered'>
        <LoadingSpinner />
      </div>
    );
  }

  if (error) {
    return <p className='centered focused'>{error}</p>;
  }

  return (
    <>
      <Switch>

        <Route exact path='/'>
          <Header/>
          <Redirect to="/ReeFood"/>
        </Route>

        <Route exact path='/auth'>
          <Auth />
        </Route>
        
        <Route path="/ReeFood">
          <Header/>
          <Home/>
        </Route>

        <Route path="/dashboard">
          <DashHeader/>
          <Dashboard/>
        </Route>

        <Route exact path='/recipe'>
          <DashHeader/>
          <AllRecipes items={loadedItems}/>
        </Route>

        <Route exact path='/recipe/recipeinfo/:recipeid'>
          <DashHeader/>
          <RecipeInfo/>
        </Route>

      </Switch>

      <Footer/>
    </>
  );
}

export default App;